
* {
	margin: 0;
	font-family: 'Turret Road'; }

.title {
	text-decoration: underline;
	display: inline-block;
	padding-bottom: 30px; }

.job {
	padding: 8px; }
.center {
	justify-content: center;
	align-items: center;
	display: flex; }

.main-title {
	font-size: 1.5rem;
	font-weight: 700;
	max-width: 180px; }

.search {
	position: fixed;
	display: flex;
	top: 0;
	left: 0;
	width: 100%;
	height: 150px;
	background-color: #F3F3F3;
	align-content: center;
	align-items: center;
	justify-content: space-between; }

.container-flex {
	display: flex; }

.main {
	margin-top: 200px;
	padding-top: 30px;
	justify-content: space-around; }


.black {
	background-color: black; }

.search-button {
	margin-left: 10px;
	padding: 5px;
	display: inline-block;
	border-style: solid;
	border-color: black;
	border-width: 1px;
	border-radius: 5px;
	background-color: white;
	flex-shrink: 0; }

a:hover, a:focus {
	font-weight: 700;
	font-size: 2rem; }



.search-button:hover, .search-button:focus {
	cursor: pointer;
	background-color: #F3F3F3; }

.search-button:active {
	background-color: black;
	color: white; }

.search-input {
	margin-right: 10px;
	padding: 5px;
	font-size: 16px;
	flex-shrink: 0; }

.search-container {
	margin-left: 20px; }

form {
	width: 100%;
	display: flex;
	align-items: center; }

.buttons-container {
	justify-content: space-around; }

@media (max-width: 780px) {

	.search-container {
		flex-direction: column;
		height: 150px;
		width: 80%;
		align-content: center;
		justify-content: space-around;
		margin-top: 0;
		margin-left: auto;
		margin-right: auto; }

	.search {
		height: 200px; }

	.search-input {
		margin: 0; }

	.search-button {
		margin: 0;
		width: 30%;
		min-width: 130px;
		flex-shrink: 0; } }



